import Vue from "vue";
import Router from "vue-router";
//页面组件
import CustomerList from "@/views/customer/list";
import CustomerLabel from "@/views/customer/label";
import GoodsCategory from "@/views/goods/category";
import GoodsList from "@/views/goods/list";
import GoodsRelease from "@/views/goods/release";
import GoodsEdit from "@/views/goods/edit";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Customer",
      redirect: "/customer/list"
    },
    {
      path: "/customer/list",
      name: "CustomerList",
      component: CustomerList
    },
    {
      path: "/customer/label",
      name: "CustomerLabel",
      component: CustomerLabel
    },
    {
      path: "/goods/category",
      name: "GoodsCategory",
      component: GoodsCategory
    },
    {
      path: "/goods/list",
      name: "GoodsList",
      component: GoodsList
    },
    {
      path: "/goods/release",
      name: "GoodsRelease",
      component: GoodsRelease
    },
    {
      path: "/goods/edit/:id",
      name: "GoodsEdit",
      component: GoodsEdit
    }
  ]
});
