<template>
	<el-form ref="form" :model="form" label-position="left" label-width="120px">
		<el-form-item label="商品分类">
			<el-select v-model="form.cate_1st" :change="cateChangeHandle(1,'cate_1st')" placeholder="请选择分类">
				<el-option v-for="item in cate_1st_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
			</el-select>
			<el-select v-model="form.cate_2nd" :change="cateChangeHandle(form.cate_2nd,'cate_2nd')" placeholder="请选择分类">
				<el-option v-for="item in cate_2nd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
			</el-select>
			<el-select v-model="form.cate_3rd" placeholder="请选择分类">
				<el-option v-for="item in cate_3rd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
			</el-select>
		</el-form-item>
	</el-form>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					cate_1st: "",
					cate_2nd: "",
					cate_3rd: "",
					hotPoint: "",
					name: "",
					price: "",
					marketPrice: "",
					cost: "",
					discount: "0",
					inventory: "",
					articleNo: "",
					img_lg: "",
					img_md: "",
					slider: "",
					brand: "",
					detail: "",
					freight: "",
					province: "山东省",
					city: "青岛市",
					area: "城阳区",
				},
				cate_1st_options: [],
				cate_2nd_options: [],
				cate_3rd_options: [],
			}
		},
		created() {
//			this.cateChangeHandle(1, 'cate_1st');
		},
		methods: {
			//分类change事件
			cateChangeHandle(id, cate) {
				if(!id) {
					return false;
				}
				this.getOptions(id).then((data) => {
					//回调函数
					this[cate + '_options'] = data;
					if(data.length == 0) {
						this.form[cate] = '';
						return false;
					}
					this.form[cate] = data[0].id;
				});
			},
			//获取下一级分类
			getOptions(id) {
				return this.axios.get('/api/category/sub/', {
						params: {
							pId: id
						}
					})
					.then(function(result) {
						if(result.data.status) {
							//回调函数
							return Promise.resolve(result.data.data);
						} else {
							this.$message.error(result.data.msg);
						}
					});
			},
		}
	};
</script>
